.weather {
  margin-top: 20px;
  padding-bottom: 30px;
  width: max-content;
}
.weather .title {
  font-size: 4rem;

  font-family: "Times New Roman", Times, serif;
}
.weather form {
  margin-top: 30px;
}
.weather form input {
  padding: 10px;
  border: 3px;
  border: 5px black;
  width: 275px;
  margin-bottom: 10px;
  color: rgb(241, 237, 237);
  background-color: black;
}

.weather form input:focus {
  border-bottom: 3px black;
}

.weather .getweather {
  padding: 4px;
  font-size: 1rem;
  float: left;
  border: none;
  border-radius: 3px;
  border: 2px solid black;
  background: black;
  cursor: pointer;
  color: whitesmoke;
}

.weather .getweather:hover {
  background: transparent;
  color: black;
}

.weather .getweather:focus {
  outline: none;
}


